.product-detail {
  max-width: 1200px;
  margin: 40px auto;
  display: flex;
  gap: 40px;
  padding: 0 20px;
  font-family: "Noto Sans KR", sans-serif;
  color: #333;
}

.product-images {
  flex: 1;
  min-width: 300px;
}

.main-swiper {
  width: 100%;
  height: 400px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 15px;
}

.main-swiper img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: #f9f9f9;
}

.thumbs-swiper {
  width: 100%;
  height: 90px;
  box-sizing: border-box;
}

.thumbs-swiper img {
  width: 100%;
  height: 80px;
  object-fit: cover;
  border-radius: 6px;
  cursor: pointer;
  transition: transform 0.3s ease;
  border: 2px solid transparent;
}

.thumbs-swiper .swiper-slide-thumb-active img {
  border-color: #007bff;
  transform: scale(1.05);
}

.product-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
  border: none;
}

.product-info h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 10px;
  color: #222;
}

.price {
  font-size: 1.2rem;
  display: flex;
  gap: 15px;
  align-items: center;
}

.original-price {
  text-decoration: line-through;
  color: #999;
}

.discount-price {
  color: #e63946;
  font-weight: 700;
}

.color-selection,
.size-selection,
.quantity-selection {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.color-selection h3,
.size-selection h3,
.quantity-selection h3,
.total-price h3 {
  font-weight: 600;
  font-size: 1.1rem;
  margin-bottom: 6px;
  color: #444;
}

.color-selection button,
.size-selection button {
  padding: 8px 15px;
  border: 1.5px solid #ccc;
  background: #fff;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.3s ease;
  user-select: none;
}

.color-selection button.selected,
.size-selection button.selected {
  border-color: #007bff;
  background-color: #e7f0ff;
  color: #007bff;
  font-weight: 700;
}

.color-selection button:hover:not(.selected),
.size-selection button:hover:not(.selected) {
  border-color: #007bff;
}

.quantity-selection p {
  display: flex;
  align-items: center;
  gap: 15px;
}

.quantity-selection button {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #007bff;
  border: none;
  color: white;
  font-size: 1.2rem;
  font-weight: 700;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.quantity-selection button:hover {
  background-color: #0056b3;
}

.quantity-selection span {
  min-width: 30px;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 600;
}

.total-price {
  font-size: 1.4rem;
  font-weight: 700;
  color: #222;
}

.action-buttons {
  display: flex;
  gap: 15px;
  margin-top: 15px;
}

.action-buttons button {
  flex: 1;
  padding: 12px 0;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 700;
  font-size: 1rem;
  transition: background-color 0.3s ease;
  user-select: none;
}

.action-buttons .buy-now {
  background-color: #28a745;
  color: white;
}

.action-buttons .buy-now:hover {
  background-color: #1e7e34;
}

.action-buttons .add-to-cart {
  background-color: #007bff;
  color: white;
}

.action-buttons .add-to-cart:hover {
  background-color: #0056b3;
}

.action-buttons .wishlist {
  background-color: #ffc107;
  color: #222;
}

.action-buttons .wishlist:hover {
  background-color: #e0a800;
}

#wrap {
  max-width: 1100px;
  margin: 40px auto;
  padding: 0 20px;
  font-family: "Noto Sans KR", sans-serif;
  color: #333;
}

.titleArea h2 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 25px;
  border-bottom: 2px solid #007bff;
  padding-bottom: 8px;
  color: #222;
}

.basket {
  width: 100%;
  overflow-x: auto;
}

.order-element {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
}

.order-element th,
.order-element td {
  padding: 12px 15px;
  text-align: center;
  vertical-align: middle;
  border-bottom: 1px solid #ddd;
  font-size: 0.95rem;
}

.order-element thead th {
  background-color: #007bff;
  color: white;
  font-weight: 700;
  letter-spacing: 0.03em;
}

.order-element tbody tr:hover {
  background-color: #f7faff;
  transition: background-color 0.3s ease;
}

.cart-item td.thumb img {
  width: 70px;
  height: 70px;
  object-fit: contain;
  border-radius: 6px;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}

.product-info strong {
  font-size: 1.1rem;
  color: #222;
}

.product-info div {
  font-size: 0.9rem;
  color: #666;
  margin-top: 6px;
}

.quantity input[type="number"] {
  width: 50px;
  padding: 6px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  text-align: center;
  font-size: 1rem;
  margin-right: 8px;
  outline-color: #007bff;
  -moz-appearance: textfield; /* number input spin button 제거 */
}

.quantity input[type="number"]::-webkit-inner-spin-button,
.quantity input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.quantity button.minus,
.quantity button.plus {
  background-color: #007bff;
  color: white;
  border: none;
  font-weight: 700;
  font-size: 1.1rem;
  width: 30px;
  height: 30px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  user-select: none;
  margin-left: 10px;
}

.quantity button.minus:hover,
.quantity button.plus:hover {
  background-color: #0056b3;
}

.price,
.total {
  font-weight: 600;
  color: #222;
  font-size: 1rem;
}
.price {
  border: none;
}

.del-button button.remove {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease;
  user-select: none;
}

.del-button button.remove:hover {
  background-color: #a71d2a;
}

.totalSummary {
  margin-top: 30px;
  max-width: 500px;
  margin-left: auto;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
}

.totalSummary table {
  width: 100%;
  border-collapse: collapse;
}

.totalSummary th,
.totalSummary td {
  padding: 14px 20px;
  text-align: center;
  font-size: 1.1rem;
  border-bottom: 1px solid #ddd;
}

.totalSummary thead th {
  background-color: #007bff;
  color: white;
  font-weight: 700;
}

.totalSummary tbody tr:hover {
  background-color: #f7faff;
}

.totalSummary span {
  font-size: 0.9rem;
  color: #28a745;
  margin-left: 8px;
  font-weight: 600;
}

.total-order {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
  margin-top: 25px;
}

.total-order button {
  padding: 12px 28px;
  font-size: 1rem;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: 700;
  transition: background-color 0.3s ease;
  user-select: none;
}

.total-order .btnSubmit {
  background-color: #28a745;
  color: white;
}

.total-order .btnSubmit:hover {
  background-color: #1e7e34;
}

.total-order .btnEm {
  background-color: #007bff;
  color: white;
}

.total-order .btnEm:hover {
  background-color: #0056b3;
}

.empty-cart {
  max-width: 600px;
  margin: 80px auto;
  text-align: center;
  color: #666;
}

.empty-cart h2 {
  font-size: 2.2rem;
  margin-bottom: 20px;
  color: #222;
}

.empty-cart p {
  font-size: 1.1rem;
}
